import { Rational } from '../struct/rational'
import gcd from 'common/math/gcd'

/**
 * 
 * 
 * @param a 
 * @param bp 
 * @param cq 
 */
export function avRescaleQ(a: int64, bq: Rational, cq: Rational) {
  const b = a * static_cast<int64>(bq.num) * static_cast<int64>(cq.den)
  const c = static_cast<int64>(bq.den) * static_cast<int64>(cq.num)
  return b / c
}

/**
 * 
 * 
 * @param a 
 * @param bp 
 * @param cq 
 */
export function avRescaleQ2(a: int64, bq: pointer<Rational>, cq: pointer<Rational>) {
  const b = a * static_cast<int64>(bq.num) * static_cast<int64>(cq.den)
  const c = static_cast<int64>(bq.den) * static_cast<int64>(cq.num)
  return b / c
}

/**
 *  double
 * 
 * @param a 
 */
export function avQ2D(a: Rational) {
  return a.num / a.den
}

/**
 *  double
 * 
 * @param a 
 */
export function avQ2D2(a: pointer<Rational>) {
  return a.num / a.den
}

/**
 *  Rational
 * 
 * @param a 
 */
export function avReduce(a: Rational) {
  const gcdValue = gcd(a.num, a.den)
  if (gcdValue <= 1) {
    return
  }
  a.den /= gcdValue
  a.num /= gcdValue
}

/**
 *  Rational
 * 
 * @param a 
 */
export function avReduce2(a: pointer<Rational>) {
  const gcdValue = gcd(a.num, a.den)
  if (gcdValue <= 1) {
    return
  }
  a.den /= gcdValue
  a.num /= gcdValue
}
