import VideoProgram from './VideoProgram'

export default abstract class RGBProgram extends VideoProgram {

  private rgbSamplerLocation: WebGLUniformLocation

  constructor(rgbFragmentSource: string) {
    super(rgbFragmentSource)
  }

  link(gl: WebGLRenderingContext) {
    super.link(gl)
    this.rgbSamplerLocation = this.gl.getUniformLocation(this.program, 'rgb_Sampler')
  }

  bindRGBTexture(unit: number = 0) {
    this.gl.uniform1i(this.rgbSamplerLocation, unit)
  }
}
